import React from "react"
import * as S from "./styled"
import sind from "../../assets/images/sind.png"

const Projects = () => {
  return (
    <S.ProjectWrapper>
      <S.TextBlock>
        <S.TitleH>
          <span className="span-name">PORTFOLIO</span>
          <span className="span-describe">Articles</span>
        </S.TitleH>

        <p>Some of the articles created for academic projects.</p>
      </S.TextBlock>
      <S.Separator />
      <ol>
        <S.ProjectItem>
          <S.ProjectFigure>
            <div className="gatsby-image">
              <div
                aria-hidden="true"
                style={{
                  width: "100%",
                  paddingBottom: "60.4651",
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <a
                  href="https://www.academia.edu/35394217/SIND_-_Sistema_de_Informa%C3%A7%C3%B5es_Nutricionais_e_Diet%C3%A9ticas?source=swp_share"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={sind}
                    alt="#"
                    style={{
                      top: 0,
                      left: 0,
                      width: "100%",
                      height: "100%",
                      objectFit: "cover",
                      objectPosition: "center center",
                      opacity: 1,
                      maxWidth: 300,
                      transition: "opacity 500ms ease 0s",
                    }}
                  />
                </a>
              </div>
            </div>
          </S.ProjectFigure>
          <S.ProjectDetails>
            <S.TextBlock>
              <S.TextTitle>
                <a
                  href="https://www.academia.edu/35394217/SIND_-_Sistema_de_Informa%C3%A7%C3%B5es_Nutricionais_e_Diet%C3%A9ticas?source=swp_share"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  SIND Software Article
                </a>
              </S.TextTitle>
              <S.TextParagraph>
                Article created for the conclusion work of ETEC Guaratinguetá
                with the development of SIND (Sistema de Informações
                Nutricionais e Dietéticas)
              </S.TextParagraph>
            </S.TextBlock>
          </S.ProjectDetails>
        </S.ProjectItem>
      </ol>
    </S.ProjectWrapper>
  )
}

export default Projects
