import styled from "styled-components"

export const ProjectWrapper = styled.div`
  max-width: 1280px;
  animation: 0.3s linear 0s 1 normal none running jBcSpD;

  @media (min-width: 768px) @media screen and (min-width: 320px) {
    padding-right: calc(48px + (16 * (100vw - 320px)) / 1120);
    padding-left: calc(48px + (16 * (100vw - 320px)) / 1120);
    margin-bottom: calc(48px + (16 * (100vw - 320px)) / 1120);
    margin-top: calc(48px + (16 * (100vw - 320px)) / 1120);
  }

  @media (min-width: 768px) {
    margin-top: -48px;
    margin-bottom: 48px;
    padding-left: 0;
    padding-right: 48px;
  }
`

export const TextBlock = styled.div`
  @media screen and (min-width: 320px) {
    font-size: calc(13px + (2 * (100vw - 320px)) / 1120);
  }

  font-size: 13px;
  line-height: 1.6em;
  font-family: Rubik, Arial, sans-serif;

  p {
    font-size: 18px;
    line-height: 1.6em;
    font-family: Rubik, Arial, sans-serif;
  }

  media screen and (min-width: 320px) {
    p {
      margin-bottom: calc(24px + (8 * (100vw - 320px)) / 1120);
    }
  }

  p {
    margin-bottom: 24px;
    font-size: calc(18px + (8 * (100vw - 320px)) / 1120);
  }

  @media screen and (min-width: 320px) {
     {
      font-size: calc(18px + (8 * (100vw - 320px)) / 1120);
    }
  }
`
export const TitleH = styled.h1`
  h1 {
    margin-bottom: 24px;
  }

  @media screen and (min-width: 320px) {
    h1 {
      margin-bottom: calc(24px + (8 * (100vw - 320px)) / 1120);
    }
  }

  .span-name {
    margin-bottom: 24px;
    font-size: 14px;
    line-height: 1.6em;
    display: block;
    letter-spacing: 0.32em;
    text-transform: uppercase;
    color: rgb(105, 105, 105);
  }

  @media screen and (min-width: 320px) {
    .span-name {
      font-size: calc(14px + (2 * (100vw - 320px)) / 1120);
      margin-bottom: calc(24px + (8 * (100vw - 320px)) / 1120);
    }
  }

  .span-describe {
    font-size: 28px;
    line-height: 1.4em;
    display: block;
    font-family: "Roboto Mono", monospace;
    word-spacing: -0.08em;
    font-weight: 500;
    letter-spacing: -0.04em;
    color: rgb(237, 237, 237);
  }

  @media screen and (min-width: 320px) {
    .span-describe {
      font-size: calc(28px + (14 * (100vw - 320px)) / 1120);
    }
  }
`

export const Separator = styled.hr`
  height: 0px;
  clear: both;
  position: relative;
  width: 100%;
  margin-top: 12px;
  margin-bottom: 12px;
  border-color: transparent;
  border-width: 0px 0px 2px;
  background: transparent;

  @media screen and (min-width: 320px) {
    margin-bottom: calc(12px + (4 * (100vw - 320px)) / 1120);
    margin-top: calc(12px + (4 * (100vw - 320px)) / 1120);
  }

  @media (max-width: 767px) {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
`

export const ProjectItem = styled.li`
  @media screen and (min-width: 320px) {
    li:not(:last-child) {
      padding-bottom: calc(48px + (16 * (100vw - 320px)) / 1120);
    }
  }

  @media (min-width: 1024px) {
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
    -webkit-box-align: center;
    align-items: center;
    margin-left: 100px;
  }
  @media (min-width: 1200px) {
    margin-left: 0;
  }
`
export const ProjectFigure = styled.figure`
  margin-bottom: 12px;
  cursor: pointer;

  @media (min-width: 1024px) @media screen and (min-width: 320px) {
    padding-left: calc(24px + (8 * (100vw - 320px)) / 1120);
    margin-bottom: calc(0px + (0 * (100vw - 320px)) / 1120);
  }

  @media (min-width: 1024px) {
    padding-left: 24px;
    margin-bottom: 0px;
    flex: 0 0 66.6667%;
  }

  @media screen and (min-width: 320px) {
    margin-bottom: calc(12px + (4 * (100vw - 320px)) / 1120);
  }

  .gatsby-image {
    position: relative;
    overflow: hidden;
  }
`

export const ProjectDetails = styled.div`
  font-size: 13px;
  line-height: 1.6em;

  @media (min-width: 1024px) {
    flex: 0 0 33.3333%;
  }
  @media screen and (min-width: 320px) {
    font-size: calc(13px + (2 * (100vw - 320px)) / 1120);
  }
`
export const TextTitle = styled.h2`
  @media screen and (min-width: 320px) {
    margin-bottom: calc(24px + (8 * (100vw - 320px)) / 1120);
  }

  a,
  span {
    font-family: inherit;
    font-size: 32px;
    line-height: 1.4em;
    color: rgb(237, 237, 237);
    font-family: Rubik, Arial, sans-serif;
    border-style: solid;
    border-width: 0px 0px 2px;
    transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
    border-color: rgb(237, 237, 237);
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    border-style: solid;
    text-decoration: none;
    transition: border-color 0.1s ease 0s, color 0.1s ease 0s;
  }
`

export const TextParagraph = styled.p`
  text-align: justify;
  margin-bottom: calc(12px + (4 * (100vw - 320px)) / 1120);
  font-size: calc(15px + (3 * (100vw - 320px)) / 1120) !important;
`
export const TextTags = styled.span`
  padding-top: 3px;
  font-weight: 500;
  letter-spacing: 0.08em;
  line-height: 1.4em;
  text-align: center;
  text-transform: uppercase;
  background-color: rgb(237, 237, 237);
  border-radius: 4px;
  color: #191919;
  padding: 5px;
  font-family: Rubik, Arial, sans-serif;
  border-style: solid;
  transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  border-style: solid;
  text-decoration: none;
  margin-right: 25px;
  transition: border-color 0.1s ease 0s, color 0.1s ease 0s;
  font-size: 18px;

  @media screen and (min-width: 320px) {
    padding-bottom: calc(3px + (1 * (100vw - 320px)) / 1120);
    padding-top: calc(3px + (1 * (100vw - 320px)) / 1120);
  }
`
